
:root {
    --mintcream: #f4fff8;
    --darkliver: #4d4847;
    --outerspacecrayola: #1c3738;
    --pewterblue: #8baaad;
    --darkgreenblack: #000f08;
    --green: #539987;
}

* {
    box-sizing: border-box;
}

footer p {
    font-size: 18px;
    text-align: center;
    background-color: var(--pewterblue);
    padding: 20px;
}

@media screen and (max-width: 980px) {
    .header {
        flex-direction: column;
        text-align: center;
    }
    .nav-links {
        flex-direction: column;
        text-align: center;
    }
    .nav-links {
        flex-direction: row;
    }
    .project-card {
        max-width: 100%;
        aspect-ratio: 2;
    }
    .homework-card {
        max-width: 100%;
        aspect-ratio: 2.5;
    }
}

@media screen and (max-width: 600 px) {

    .row h2 {
        min-width: 20%;
        font-size: smaller;
    }
    .project-title {
        font-size: smaller;
    }

    .contact-links a {
        font-size: smaller;
    }

}